import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Row, Col, Alert, InputNumber } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class LocationUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { location } = this.props;
    const {
      loading,
      loadingSubmit,
      location_types,
      sites,
      accounts
    } = location;
    const data = location.location ? location.location : [];
    var phone = data ? (data.phone ? data.phone + "" : null) : [];
    phone = phone ? phone.substring(3) : null;

    const error = location.errorField ? location.errorField : [];
    const used = data.is_used;
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="locations.add.subtitle.data" />
        </Divider> */}
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="locations.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.code" />}
              validateStatus={
                error.location_code || getFieldError("location_code")
                  ? "error"
                  : null
              }
              help={
                error.location_code
                  ? error.location_code
                  : getFieldError("location_code")
              }
            >
              {getFieldDecorator("location_code", {
                initialValue: data.location_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 10,
                    message: <IntlMessages id="maxLength10" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  // style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.name" />}
            >
              {getFieldDecorator("location_name", {
                initialValue: data.location_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.locationType" />}
                >
                  {getFieldDecorator("id_location_type", {
                    initialValue: data.id_location_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={
                        location_types.length > 0 && data.id_location_type
                          ? location_types.find(
                              x => x.id_location_type === data.id_location_type
                            ).is_ho === 1
                            ? true
                            : false
                          : false
                      }
                    >
                      {location_types.map((x, key) => (
                        <Option key={key} value={x.id_location_type}>
                          {x.location_type_name}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.site" />}
                >
                  {getFieldDecorator("id_site", {
                    initialValue: data.id_site,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                    >
                      {sites.map((data, key) => (
                        <Option key={key} value={data.id_site}>
                          {data.site_name}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              {/* <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.costingMethod" />}
                >
                  {getFieldDecorator("costing_method", {
                    initialValue: data.costing_method,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      style={radioGroupStyle}
                      //disabled={used ? true : false}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}>
                        Average
                      </Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={2}>FIFO</Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col> */}
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.inventoryType" />}
                >
                  {getFieldDecorator("inventory_type", {
                    initialValue: data.inventory_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group style={radioGroupStyle} buttonStyle="solid">
                      <Radio.Button style={radioButtonStyle} value={1}>
                        Normal
                      </Radio.Button>
                      {/* <Radio.Button style={radioButtonStyle} value={2}>Minus</Radio.Button> */}
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.leadTime" />}
                >
                  {getFieldDecorator("lead_time", {
                    initialValue: data.lead_time,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(<InputNumber min={0} style={{ width: "100%" }} />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.account" />}
                >
                  {getFieldDecorator("id_account_item_transfer", {
                    initialValue: data.id_account_item_transfer,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                    >
                      {accounts.map((data, key) => (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxNumber" />}
            >
              {getFieldDecorator("tax_number", {
                initialValue: data.tax_number,
                rules: [
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </div>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            <Form.Item {...formTextArea} label={<IntlMessages id="address" />}>
              {getFieldDecorator("address", {
                initialValue: data.address,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <TextArea
                  autosize={{ minRows: 4 }}
                  style={{ marginBottom: "8px" }}
                />
              )}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.email" />}
                >
                  {getFieldDecorator("email", {
                    initialValue: data.email,
                    rules: [
                      {
                        type: "email",
                        message: <IntlMessages id="invalidEmail" />
                      }
                    ]
                  })(<Input />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.phone" />}
                >
                  {getFieldDecorator("phone", {
                    initialValue: data.phone,
                    rules: [
                      {
                        max: 20,
                        message: <IntlMessages id="maxLength20" />
                      },
                      {
                        pattern: "^[0-9]*$",
                        message: <IntlMessages id="invalidPhone" />
                      }
                    ]
                  })(
                    <Input
                      //addonBefore="+62"
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.url" />}
            >
              {getFieldDecorator("url", {
                initialValue: data.url
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description
              })(<TextArea autosize={{ minRows: 4 }} />)}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedLocationUpdateForm = Form.create({ name: "update_location" })(
  LocationUpdateForm
);

export default WrappedLocationUpdateForm;
