import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import LocationUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import locationActions from "../../../../redux/companyMaster/location/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/companies/locations";


class UpdateLocation extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editLocation } = this.props;
    const { id } = this.props.match.params;
    editLocation(id);
  }

  componentDidUpdate() {
    // const { id } = this.props.match.params;

    // Display error notif
    if (this.props.location.error) {
      notification("error", this.props.location.error);
      this.props.clearError();
    }

    if (this.props.location.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateLocation } = this.props;
    const { id } = this.props.match.params;
    updateLocation(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="locations.update.title" />}
            style={{ margin: 0 }}
          >
            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.location.loading && !this.props.location.loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <LocationUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                location={this.props.location}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, location: state.location };
};

export default compose(
  connect(
    mapStateToProps,
    locationActions
  )
)(UpdateLocation);
